"use client"

import type React from "react"

import { useState, useRef, useEffect } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { useChat } from "@ai-sdk/react"
import { ArrowLeft, Save, FileEdit, MessageSquare } from "lucide-react"
import { Button } from "@/components/ui/button"
import { MessageBubble } from "@/components/chat/message-bubble"
import { TypingIndicator } from "@/components/chat/typing-indicator"
import { ChatInput } from "@/components/chat/chat-input"
import { NotesPanel } from "@/components/notes/notes-panel"
import { useSOPContext } from "@/lib/sop-context"
import type { Note, SOP } from "@/lib/mock-data"
import { cn } from "@/lib/utils"

function parseAIResponse(content: string): { message: string; notes: Note[]; phase: string; progress: number } {
  try {
    // Try to parse as JSON first
    const parsed = JSON.parse(content)
    const notes: Note[] = (parsed.notes || []).map((n: any, idx: number) => ({
      id: `note-${Date.now()}-${idx}`,
      category: n.category || "OTHER",
      priority: n.priority || "medium",
      timestamp: new Date().toLocaleTimeString(),
      source: "AI Extraction",
      content: n.content || "",
      relatedTo: n.relatedTo || "",
      action: n.action || "",
    }))
    return {
      message: parsed.message || content,
      notes,
      phase: parsed.phase || "foundation",
      progress: parsed.progress || 0,
    }
  } catch {
    // If not JSON, return as plain message
    return { message: content, notes: [], phase: "foundation", progress: 0 }
  }
}

export default function CreateSOPPage() {
  const router = useRouter()
  const { addSOP } = useSOPContext()
  const [notes, setNotes] = useState<Note[]>([])
  const [currentPhase, setCurrentPhase] = useState("foundation")
  const [progress, setProgress] = useState(0)
  const [activeTab, setActiveTab] = useState<"chat" | "notes">("chat")
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const { messages, input, handleInputChange, handleSubmit, isLoading, setMessages } = useChat({
    api: "/api/chat/sop",
    initialMessages: [
      {
        id: "initial",
        role: "assistant",
        content: JSON.stringify({
          message:
            "Hello! I'm here to help you create a professional SOP. Let's start with the basics.\n\nWhat process or procedure would you like to document today?",
          notes: [],
          phase: "foundation",
          progress: 0,
        }),
      },
    ],
    onFinish: (message) => {
      // Extract notes from completed AI response
      const parsed = parseAIResponse(message.content)
      if (parsed.notes.length > 0) {
        setNotes((prev) => [...prev, ...parsed.notes])
      }
      if (parsed.phase) {
        setCurrentPhase(parsed.phase)
      }
      if (parsed.progress) {
        setProgress(parsed.progress)
      }
    },
  })

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages, isLoading])

  const getDisplayMessage = (content: string) => {
    try {
      const parsed = JSON.parse(content)
      return parsed.message || content
    } catch {
      return content
    }
  }

  const handleSaveDraft = () => {
    const newSOP: SOP = {
      id: `sop-${Date.now()}`,
      title: "New SOP Draft",
      department: "General",
      status: "draft",
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      content: "",
      notes,
      chatHistory: messages.map((m) => ({
        id: m.id,
        role: m.role === "assistant" ? "ai" : "user",
        content: getDisplayMessage(m.content),
        timestamp: new Date().toISOString(),
      })),
    }
    addSOP(newSOP)
    router.push("/dashboard")
  }

  const handleReview = () => {
    const sopId = `sop-${Date.now()}`
    const newSOP: SOP = {
      id: sopId,
      title: "New SOP",
      department: "General",
      status: "draft",
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      content: "",
      notes,
      chatHistory: messages.map((m) => ({
        id: m.id,
        role: m.role === "assistant" ? "ai" : "user",
        content: getDisplayMessage(m.content),
        timestamp: new Date().toISOString(),
      })),
    }
    addSOP(newSOP)
    router.push(`/review/${sopId}`)
  }

  const handleDeleteNote = (id: string) => {
    setNotes((prev) => prev.filter((n) => n.id !== id))
  }

  const handleSend = (content: string) => {
    // Create a synthetic event to trigger handleSubmit
    const fakeEvent = {
      preventDefault: () => { },
    } as React.FormEvent<HTMLFormElement>

    // Set the input value and submit
    handleInputChange({ target: { value: content } } as React.ChangeEvent<HTMLInputElement>)
  }

  const pendingMessageRef = useRef<string | null>(null)

  const sendMessage = (content: string) => {
    pendingMessageRef.current = content
    handleInputChange({ target: { value: content } } as React.ChangeEvent<HTMLInputElement>)
  }

  useEffect(() => {
    if (pendingMessageRef.current && input === pendingMessageRef.current) {
      const fakeEvent = { preventDefault: () => { } } as React.FormEvent<HTMLFormElement>
      handleSubmit(fakeEvent)
      pendingMessageRef.current = null
    }
  }, [input, handleSubmit])

  return (
    <div className="min-h-screen bg-muted/30 flex flex-col">
      {/* Header */}
      <header className="bg-background border-b border-border px-4 py-3 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <Link
            href="/dashboard"
            className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-colors"
          >
            <ArrowLeft className="w-5 h-5" />
            <span className="hidden sm:inline">Back to Dashboard</span>
          </Link>
        </div>
        <div className="flex items-center gap-2">
          <h1 className="text-lg font-semibold text-foreground">Creating New SOP</h1>
          <span className="hidden sm:inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-primary/10 text-primary capitalize">
            {currentPhase.replace("_", " ")}
          </span>
        </div>
        <Button variant="outline" onClick={handleSaveDraft} className="gap-2 bg-transparent">
          <Save className="w-4 h-4" />
          <span className="hidden sm:inline">Save Draft</span>
        </Button>
      </header>

      {/* Mobile Tab Navigation */}
      <div className="lg:hidden flex border-b border-border bg-background">
        <button
          onClick={() => setActiveTab("chat")}
          className={cn(
            "flex-1 py-3 text-sm font-medium flex items-center justify-center gap-2 transition-colors",
            activeTab === "chat"
              ? "text-primary border-b-2 border-primary"
              : "text-muted-foreground hover:text-foreground",
          )}
        >
          <MessageSquare className="w-4 h-4" />
          Chat
        </button>
        <button
          onClick={() => setActiveTab("notes")}
          className={cn(
            "flex-1 py-3 text-sm font-medium flex items-center justify-center gap-2 transition-colors",
            activeTab === "notes"
              ? "text-primary border-b-2 border-primary"
              : "text-muted-foreground hover:text-foreground",
          )}
        >
          <FileEdit className="w-4 h-4" />
          Notes ({notes.length})
        </button>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex overflow-hidden">
        {/* Chat Area */}
        <div
          className={cn(
            "flex-1 flex flex-col bg-background",
            activeTab === "notes" ? "hidden lg:flex" : "flex",
            "lg:border-r border-border",
          )}
        >
          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 lg:p-6">
            {messages.map((message) => (
              <MessageBubble
                key={message.id}
                message={{
                  id: message.id,
                  role: message.role === "assistant" ? "ai" : "user",
                  content: getDisplayMessage(message.content),
                  timestamp: new Date().toISOString(),
                }}
              />
            ))}
            {isLoading && <TypingIndicator />}
            <div ref={messagesEndRef} />
          </div>

          {/* Input */}
          <ChatInput onSend={sendMessage} disabled={isLoading} />
        </div>

        {/* Notes Panel - Desktop */}
        <div className={cn("w-96 hidden lg:flex flex-col")}>
          <NotesPanel
            notes={notes}
            onDeleteNote={handleDeleteNote}
            onReview={handleReview}
            progress={progress}
            phase={currentPhase}
          />
        </div>

        {/* Notes Panel - Mobile */}
        <div className={cn("flex-1 lg:hidden", activeTab === "chat" ? "hidden" : "flex flex-col")}>
          <NotesPanel
            notes={notes}
            onDeleteNote={handleDeleteNote}
            onReview={handleReview}
            progress={progress}
            phase={currentPhase}
          />
        </div>
      </div>
    </div>
  )
}
